html,
body {
  user-select: none;
  font-family: 'Open Sans', Arial, Helvetica, sans-serif;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  overflow: hidden;
  position: fixed;
}
::-webkit-input-placeholder {
  color: #4d90fe;
}
::-moz-placeholder {
  color: #4d90fe;
  padding: 50;
}

/* firefox 19+ */
:-ms-input-placeholder {
  color: #4d90fe;
}

input,
textarea,
select,
input + label {
  font-family: inherit;
  font-size: 14px;
}

select,
input,
input + label {
  border-radius: 0;
  padding: 6px;
  -ms-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  display: block;
  outline-width: 1px;
  outline-style: solid;
  border-color: transparent;
  outline-color: #888888;
  margin-bottom: 8px;
  border-width: 2px;
  border-style: inset;
}

input,
input + label {
  word-break: break-all;
}

/* ie */
input:-moz-placeholder {
  color: #4d90fe;
}

select:focus,
input:focus {
  outline-color: #4d90fe;
  outline-width: 2px;
  outline-style: solid;
  border-color: transparent;
}

.pageBody {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.controlcontainer {
  display: flex;
  flex-direction: column;
  padding: 8px;
}

.input_container {
  display: flex;
  flex-direction: column;
}

.compareoption {
  display: flex;
  flex-direction: column;
}

.inputfile {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}

.inputfile + label {
  font-size: 14px;
}

.compareviewercontainer,
.compareviewer {
  flex-grow: 1;
  display: flex;
}

.comparebutton {
  padding: 8px;
}
